<template>
  <v-container class="fill-height" fluid>
    <v-row align="center">
      <v-col cols="12" sm="12">
        <v-data-table :headers="headers" :items="settings" sort-by="name" class="elevation-1">
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title>My Settings</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="500px">
                <v-card>
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="4" md="4">
                          <v-text-field v-model="editedItem.name" label="Settings name" readonly></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="8" md="8">
                          <v-text-field v-model="editedItem.value" label="Value"></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                    <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="initialize">Reset</v-btn>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    headers: [
      { text: "Settings Name", value: "name" },
      { text: "Value", value: "value" },
      { text: "Actions", value: "actions", sortable: false }
    ],
    settings: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      value: 0
    },
    defaultItem: {
      name: "",
      value: 0
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.settings = [
        {
          name: "PLC_IP",
          value: "10.0.0.1"
        },
        {
          name: "PLC_SERVER_PORT",
          value: "10.0.0.1"
        },
        {
          name: "PLC_ADDRESS",
          value: "10.0.0.1"
        },
        {
          name: "GOOD_MESSAGE",
          value: "10.0.0.1"
        },
        {
          name: "BLANK_MESSAGE",
          value: "10.0.0.1"
        },
        {
          name: "WEIGHT_PATH",
          value: "10.0.0.1"
        },
        {
          name: "API_URL",
          value: "http://localhost:8080"
        },
        {
          name: "PRED_THRESHOLD",
          value: 408
        },
        {
          name: "IG_MAX_FPS",
          value: 452
        }
      ];
    },

    editItem(item) {
      this.editedIndex = this.settings.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.settings.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.settings.splice(index, 1);
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.settings[this.editedIndex], this.editedItem);
      } else {
        this.settings.push(this.editedItem);
      }
      this.close();
    },
  }
};
</script>
